import request from '@/common/utils/request';
import { buildUrlWithObj } from '@/common/utils/url-utils';

export function queryTransactionList(data) {
  return request({
    url: '/fintech/v1/transaction/orderList',
    method: 'post',
    data,
  });
}

export function queryDetail(data) {
  return request({
    url: '/fintech/v1/transaction/order/orderDetail',
    method: 'post',
    data,
  });
}

// export transactionList
export function exportOrderList(data) {
  return request({
    url: '/fintech/v1/transaction/orderList/export',
    method: 'post',
    data,
    responseType: 'blob',
  });
}

