<template>
  <div class="business-portal-page">
    <!-- title -->
    <portal-header :projectObj="titleConfig"></portal-header>
    <!-- body -->
    <portal-form
      ref="searchList"
      :form-data="searchList"
      :form-config-list="formConfigList"
      :searchBtnLoading="searchLoading"
      :searchBtnDisabled="!hasPerm('TC_ORGTRANSACTION_REVIEW_REVERSE_REQUEST_BTN')"
      @common-enter-event="search"
      @reset-btn-click="reset"
      @search-btn-click="search"
    />
    <div class="table-list-container">
      <el-dropdown @command="handleExportClick">
        <el-button :disabled="exportDisabled" type="primary" size="small" class="margin-bottom-16" v-preventReClick>
          {{ $t('common.button.export') }}
          <i class="el-icon-arrow-down el-icon--right" style="font-size: 12px"></i>
        </el-button>
        <el-dropdown-menu slot="dropdown">
          <el-dropdown-item command="Excel">Excel</el-dropdown-item>
          <el-dropdown-item command="Excel2007">Excel 2007</el-dropdown-item>
          <el-dropdown-item command="CSV">CSV</el-dropdown-item>
          <el-dropdown-item command="PDF">PDF</el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>

      <table-page
        size="small"
        ref="data-table"
        class="table-page"
        :has-pagination="hasPagination"
        :columns="columns"
        :pagination="paginationConfig"
        :getTableData="getTableData"
        :is-search="refreshTableFlag"
      >
        <template slot="orderIdOperation" slot-scope="scope">
          <el-button type="text" @click="showDetail(scope.data.row)">
            {{ scope.data.row.tradeOrderID }}
          </el-button>
        </template>
        <template slot="status" slot-scope="scope">
          <span class="status-circle" :style="statusStyle(scope.data.row.status)"></span>
          {{ showStatus(scope.data.row.status) }}
        </template>
        <template v-if="isShowReverse(scope.data.row)" slot="operation" slot-scope="scope">
          <el-button type="text" class="btn" @click="reverse(scope.data.row)" style="padding: 0">
            {{ $t('IDC.reviewTransaction.button.reverse') }}
          </el-button>
        </template>
      </table-page>
    </div>
  </div>
</template>

<script>
import PortalHeader from '@/components/portal-header';
import dayjs from 'dayjs';
import TablePage from '@/components/tablePage';
import PortalForm from '@/components/portal-form';
import { queryReversalRequestList } from '@/views/identifier-center/api/api-reversal-request.js';
import { exportReversalRequest } from '@/views/identifier-center/api/api-transaction.js';
import cookie from '@/common/utils/cookie';
import { dateFormat } from '@/common/utils/date-time-utils';

export default {
  name: 'ReviewReversalRequest',
  components: { PortalHeader, TablePage, PortalForm },
  props: {
    activeName: {
      type: String,
      default: '',
    },
    identityId: {
      type: String,
      default: '',
    },
  },
  data() {
    return {
      identityType: '',
      entityIdentityId: this.identityId,
      searchLoading: false,
      searchVisible: false,
      refreshTableFlag: false,
      hasPagination: true,
      exportDisabled: true,
      totalRecords: 0,
      searchList: {
        timePeriod: '',
        queryMode: 'both',
      },
      columns: [
        {
          label: this.$t('IDC.reviewReversalRequest.label.requestId'),
          prop: 'tradeOrderID',
          align: 'left',
          __slotName: 'orderIdOperation',
          'min-width': 150,
        },
        {
          label: this.$t('IDC.reviewReversalRequest.label.creationTime'),
          prop: 'createTime',
          align: 'left',
          'min-width': 200,
        },
        {
          label: this.$t('IDC.reviewReversalRequest.label.completionTime'),
          prop: 'completeTime',
          align: 'left',
          'min-width': 133,
        },
        {
          label: this.$t('IDC.reviewReversalRequest.label.status'),
          prop: 'status',
          align: 'left',
          __slotName: 'status',
          'min-width': 123,
        },
        {
          label: this.$t('IDC.reviewReversalRequest.label.initiator'),
          prop: 'initiatorPublicName',
          align: 'left',
          'min-width': 193,
        },
        {
          label: this.$t('IDC.reviewReversalRequest.label.receiver'),
          prop: 'receiverPublicName',
          align: 'left',
          'min-width': 193,
        },
        {
          label: this.$t('IDC.reviewReversalRequest.label.currency'),
          prop: 'currency',
          align: 'left',
          'min-width': 150,
        },
        {
          label: this.$t('common.label.operation'),
          prop: 'operation',
          align: 'left',
          fixed: 'right',
          __slotName: 'operation',
          'min-width': 93,
        },
      ],
      categoryOptions: [
        { label: 'All', value: 'both' },
        { label: 'Received', value: 'receiver' },
        { label: 'Sent', value: 'requester' },
      ],
      paginationConfig: {
        total: 0,
        pageIndex: 1,
        pageSize: 10,
      },
    };
  },
  computed: {
    titleConfig() {
      return {
        title: this.$t('IDC.reviewReversalRequest.pageTitle'),
        desc: this.$t('IDC.reviewReversalRequest.pageDesc'),
      };
    },
    formConfigList() {
      return [
        {
          formItemClass: 'form-special-date-box',
          sameGroup: true,
          label: this.$t('IDC.reviewReversalRequest.label.timePeriod'),
          prop: 'timePeriod',
          comp: [
            {
              key: 'timePeriod',
              comp: 'el-date-picker',
              bind: {
                id: 'timePeriod',
                type: 'datetimerange',
                format: 'dd/MM/yyyy HH:mm:ss',
                'range-separator': '~',
                'default-time': ['00:00:00', '23:59:59'],
                'start-placeholder': 'Start',
                'end-placeholder': 'End',
              },
            },
            {
              comp: 'i',
              class: 'el-icon-date data_icon',
            },
          ],
        },
        {
          label: this.$t('IDC.reviewReversalRequest.label.category'),
          key: 'queryMode',
          comp: 'el-select',
          options: this.categoryOptions,
          bind: {
            placeholder: this.$t('common.placeholder.select'),
            id: 'category',
          },
        },
      ];
    },
    statusStyle() {
      return (status) => {
        switch (status) {
          case '00':
            return { 'background-color': `${this.GlobalStyleVariables.statusNoticeColor}` };
          default:
            return { 'background-color': `${this.GlobalStyleVariables.statusSuccessColor}` };
        }
      };
    },
  },
  watch: {},
  created() {
    this.identityType = '5000';
    if (this.identityId) {
      this.entityIdentityId = this.identityId;
    } else {
      this.entityIdentityId = cookie.get('organizationIdentityId');
    }
  },
  activated() {},
  mounted() {},
  methods: {
    isShowReverse(row) {
      return row.status === '00';
    },

    reset() {
      this.searchList = {
        timePeriod: '',
        queryMode: 'both',
      };
      this.search();
    },
    search() {
      this.$refs['searchList'].innerFormRef.validate((valid) => {
        if (!valid) {
          this.searchLoading = false;
          return;
        }
        this.paginationConfig = {
          total: 0,
          pageIndex: 1,
          pageSize: 10,
        };
        this.refreshTableFlag = true;
      });
    },
    showDetail(row) {
      let params = {
        tradeOrderId: row.tradeOrderID,
        identityId: this.entityIdentityId,
        identityType: this.identityType,
        type: 'detail',
        from: 'reversalRequest',
      };
      const path = '/mainPage/identifierCenter/organizationInfo/organization/transactionDetail';
      this.openNewWindow({ path: path, query: { params: encodeURIComponent(JSON.stringify(params)) } });
    },

    reverse(row) {
      let params = {
        tradeOrderId: row.tradeOrderID,
        identityId: this.entityIdentityId,
        identityType: this.identityType,
        type: 'reverse',
        from: 'reversalRequest',
      };
      const path = '/mainPage/identifierCenter/organizationInfo/organization/transactionDetail';
      this.openNewWindow({ path: path, query: { params: encodeURIComponent(JSON.stringify(params)) } });
    },
    getTableData() {
      let params = {
        receiverID: this.entityIdentityId,
        queryMode: this.searchList.queryMode,
        createTimeBegin: this.searchList.timePeriod
          ? dayjs(this.searchList.timePeriod[0]).format('YYYY-MM-DD HH:mm:ss')
          : '',
        createTimeEnd: this.searchList.timePeriod
          ? dayjs(this.searchList.timePeriod[1]).format('YYYY-MM-DD HH:mm:ss')
          : '',
        startNumber: this.paginationConfig.pageIndex,
        pageSize: this.paginationConfig.pageSize,
      };
      this.searchLoading = true;
      return new Promise((resolve) => {
        queryReversalRequestList(params)
          .then((res) => {
            if (res.header && res.header.success) {
              res.totalRecords = res.totalSize || 0;
              res.list = res.reversalRequestDTOs || [];
              res.list = res.list.map((order) => {
                if (order.createTime) {
                  order.createTime = dayjs(order.createTime).format('DD/MM/YYYY HH:mm:ss');
                }
                if (order.completeTime) {
                  order.completeTime = dayjs(order.completeTime).format('DD/MM/YYYY HH:mm:ss');
                }
                return order;
              });
            } else {
              res.totalRecords = 0;
              res.list = [];
            }
            this.exportDisabled = res.list.length > 0 ? false : true;
            resolve(res);
          })
          .catch(() => {
            resolve();
          })
          .finally(() => {
            this.refreshTableFlag = false;
            this.searchLoading = false;
          });
      });
    },

    handleExportClick(fileType) {
      this.$refs['searchList'].validate((valid) => {
        if (valid) {
          let params = {
            receiverID: this.entityIdentityId,
            queryMode: this.searchList.queryMode,
            createTimeBegin: this.searchList.timePeriod
              ? dayjs(this.searchList.timePeriod[0]).format('YYYY-MM-DD HH:mm:ss')
              : '',
            createTimeEnd: this.searchList.timePeriod
              ? dayjs(this.searchList.timePeriod[1]).format('YYYY-MM-DD HH:mm:ss')
              : '',
            exportType: fileType,
          };
          exportReversalRequest(params)
            .then((res) => {
              if (fileType === 'Excel2007') {
                fileType = 'XLS';
              } else if (fileType === 'Excel') {
                fileType = 'XLSX';
              }
              if (res.type === 'application/json') {
                const reader = new FileReader();
                reader.readAsText(res);
                const _this = this;
                reader.onload = function () {
                  const errorData = JSON.parse(reader.result);
                  _this.$modal.msgError(errorData.header.responseDesc);
                };
              } else {
                const start = new Date();
                const date = dateFormat(start, 'YYYYMMDDHHmmss');
                const blob = new Blob([res], { type: 'application/ms-excel' }); // 解析后端返回的乱码
                const elink = document.createElement('a');
                elink.download = 'REVERSAL_REQUEST_' + date + '.' + fileType.toLowerCase(); // 定义下载名字
                elink.style.display = 'none'; // 决定是否隐藏
                elink.href = URL.createObjectURL(blob);
                document.body.appendChild(elink);
                elink.click();
                URL.revokeObjectURL(elink.href); // 释放URL 对象
                document.body.removeChild(elink);
              }
            })
            .catch()
            .finally();
        }
      });
    },
    showStatus(val) {
      switch (val) {
        case '00':
          return 'New';
        default:
          return 'Completed';
      }
    },
  },
};
</script>

<style scoped lang="scss">
/deep/ .el-table__fixed-right::before,
.el-table__fixed::before {
  height: 0;
  background: transparent;
}
</style>
